<script>
import { useTemplateStore } from '@/store'
import { mapState, mapWritableState } from 'pinia'

import EditorCanvas from './components/Canvas/Canvas.vue'
import EditorFooter from './components/Footer.vue'
import EditorHeader from './components/Header.vue'
import Attributes from './components/Tabs/Attributes.vue'
import Backgrounds from './components/Tabs/Backgrounds.vue'
import Images from './components/Tabs/Images.vue'
import Signatures from './components/Tabs/Signatures.vue'
import Templates from './components/Tabs/Templates.vue'
import TabsSelector from './components/TabsSelector.vue'

import { TemplateEditorTabs } from '@/store/TemplateStore'

export default {
  components: {
    Attributes,
    Backgrounds,
    EditorCanvas,
    EditorFooter,
    EditorHeader,
    Images,
    Signatures,
    Templates,
    TabsSelector,
  },
  data() {
    return {
      initialColor: '#ffffff',
    }
  },
  computed: {
    ...mapState(useTemplateStore, [
      'isPrinting',
      'selectedTab',
      'layoutBackgroundColor',
    ]),
    ...mapWritableState(useTemplateStore, [
      'selectedTab',
    ]),
  },
  methods: {
    colorChange() {
      if (this.layoutBackgroundColor !== this.initialColor) {
        this.initialColor = this.layoutBackgroundColor
        this.selectedTab = TemplateEditorTabs.ATTRIBUTES
      }
    },
  },
}
</script>

<template>
  <div class="template-editor">
    <Header />
    <div class="body">
      <TabsSelector />
      <div class="tabs">
        <component :is="selectedTab" />
      </div>
      <Canvas :editable="!isPrinting" />
    </div>
    <Footer />
  </div>
</template>

