<script>
import { useAccountStore, useFileStore, useGlobalStore, useTemplateStore } from '@/store'
import { mapState } from 'pinia'

import debug from 'debug'

import { UPLOAD_FILE } from '@/store/actions'

const logDebug = debug('Tabs:Images')
export default {
  computed: {
    ...mapState(useAccountStore, ['cid']),
    ...mapState(useTemplateStore, ['template', 'uploadedImages', 'activeTemplateSide', 'componentIDCount', 'activeComponent', 'components']),
  },
  methods: {
    uploadImage() {
      this.$refs.uploadImage.click()
    },
    async newImage(event) {
      const result = await useFileStore()[UPLOAD_FILE]({
        file: event.target.files[0],
        folder: `${this.cid}`,
      }).catch((err) => {
        console.error(err)
      })
      useTemplateStore().addUploadedImage(result)
    },
    addImage(image) {
      if (this.activeComponent.type === 'image') {
        this.activeComponent.src = image
      } else {
        logDebug('addImage', image)
        logDebug('componentIDCount', this.componentIDCount)
        const imageComponent = {
          id: this.componentIDCount,
          type: 'image',
          templateSide: this.activeTemplateSide,
          src: image,
          x: 50,
          y: 50,
          size: 0.4,
          isDraggable: false,
          isEditable: false,
          styles: {
          },
        }
        this.components.push(imageComponent)

        useTemplateStore().setActiveComponent(imageComponent)
      }
    },
  },
}
</script>

<template>
  <div class="images">
    <div>{{ $t('images.title') }}</div>
    <input
      ref="uploadImage"
      type="file"
      style="display: none"
      accept="image/*"
      @change="newImage"
    />
    <button @click="uploadImage">
      {{ $t('images.buttonUpload') }}
    </button>

    <hr />

    <div class="instructions">
      <strong>{{ $t('images.uploaded') }}</strong>
    </div>

    <div v-for="(image, index) in uploadedImages" :key="index">
      <img
        style="min-width: 100%; max-width: 100%; border: 1px solid #f1f1f1"
        :src="uploadedImages[index]"
        alt=""
        @click="addImage(image)"
      />
    </div>
  </div>
</template>

