<script>
import { useAccountStore } from '@/store'
import { mapState, mapWritableState } from 'pinia'

import debug from 'debug'

import Badge from '../../assets/Layouts/badge-circular.vue'
import A4 from '../../assets/Layouts/rectangle-a4.vue'
import NFT from '../../assets/Layouts/rectangle-nft.vue'

import * as designPresets from '@/constants/designPresets'
import { DEFAULT_ACTIVE_COMPONENT, useTemplateStore } from '@/store/TemplateStore'

const logDebug = debug('TemplateEditor:Tabs:Templates')

export default {
  components: {
    A4,
    Badge,
    NFT,
  },

  data() {
    return {
      customTemplate: { 'wallid.io': true },
    }
  },
  computed: {
    ...mapState(useAccountStore, ['adminEmail']),
    ...mapState(useTemplateStore, ['currentLayout']),
    ...mapWritableState(useTemplateStore, ['backSide', 'activeComponent', 'layoutBackgroundColor', 'components']),
    layouts() {
      if (
        this.adminEmail
        && this.customTemplate[this.getEmail(this.adminEmail).domain]
      ) {
        return ['A4', 'Badge', 'NFT']
      } else {
        return ['A4', 'Badge']
      }
    },
  },
  methods: {
    changeLayout(layout) {
      logDebug('Change Layout', layout)
      // FIXME: maybe this should be changed to use setTemplate
      this.currentLayout = layout
      this.backSide = false
      // this.setActiveComponent()
      this.setActiveComponent(DEFAULT_ACTIVE_COMPONENT)
      this.layoutBackgroundColor = '#29969E'
      this.components = []
      if (layout !== 'NFT') {
        this.components.push({
          id: 0,
          type: 'qrcode',
          templateSide: 'front',
          x: 94.4,
          y: 92.4,
          isDraggable: false,
          styles: {},
        })
      }
      if (layout === 'A4') {
        logDebug(
          'Set nft components for ',
          this.currentLayout,
        )
        useTemplateStore().setTemplate(designPresets.AssociateCertificateDesign)
      }
    },
    isCurrentLayout(layout) {
      return layout === this.currentLayout
    },
  },

}
</script>

<template>
  <div style="overflow: scroll !important; padding: 1.5rem; min-height: 40rem">
    <!-- Tab Title -->
    <div>{{ $t('templates.title') }}</div>
    <!-- Layouts -->
    <!-- ADD: Custom ratio layout -->
    <div
      v-for="layout in layouts"
      :key="layout"
      style="overflow: visible !important"
      @click="changeLayout(layout)"
    >
      <div class="layout-name">
        {{ layout }}
      </div>
      <component
        :is="layout"
        :highlight="isCurrentLayout(layout)"
        background="#ffffff"
        style="overflow: visible !important"
      />
    </div>
  </div>
</template>

<style scoped>
.layout-name {
  font-size: 12px;
  font-weight: 700;
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
}
</style>
