<script>
import { useTemplateStore } from '@/store'
import { mapState, mapWritableState } from 'pinia'

import debug from 'debug'

import IconArrowBack from '../assets/icon-arrow-back.vue'
import IconEdit from '../assets/icon-edit.vue'
import MessageModal from '../modals/MessageModal.vue'

import { TemplateEditorTabs } from '@/store/TemplateStore'
import { TEMPLATE_EDITOR_CREATE } from '@/store/actions'

const logDebug = debug('template-editor:header')

export default {
  components: {
    IconArrowBack,
    IconEdit,
    MessageModal,
  },
  data() {
    return {
      visibleWarning: false,
      showConfirmCreateModal: false,
      checkIssueConditions: false,
      message: 'confirm',
    }
  },
  computed: {
    ...mapWritableState(useTemplateStore, ['templateName', 'template', 'isPrinting', 'components', 'selectedTab']),
    ...mapState(useTemplateStore, ['currentLayout', 'repeatedAttributes', 'hasDynamicAttributes']),
    templateName: {
      get() {
        return this.templateName
      },
      set(value) {
        useTemplateStore().setTemplateName(value)
      },
    },
  },
  methods: {
    handleClose() {
      switch (this.message) {
        case 'confirm':
          this.showConfirmCreateModal = false
          break
        case 'successCreateTemplate':
          logDebug('handleClose', this.tid)
          if (this.tid) {
            this.isPrinting = false

            this.$router.push({
              name: `ViewTemplate`,
              params: { tid: this.tid },
            })
          }
          clearTimeout(this.timeout)
          break
      }
    },
    verifyCreateTemplateConditions() {
      logDebug('verifyCreateTemplateConditions', this.hasDynamicAttributes)
      if (!this.hasDynamicAttributes) {
        this.selectedTab = TemplateEditorTabs.ATTRIBUTES
      }
      if (this.canCreateTemplate()) {
        this.showConfirmCreateModal = true
      } else {
        this.checkIssueConditions = true
      }
    },
    async toDataURL(url) {
      if (url.startsWith('data:image')) {
        return url
      }

      return await new Promise((resolve) => {
        const xhr = new XMLHttpRequest()
        xhr.onload = function () {
          const reader = new FileReader()
          reader.onloadend = function () {
            resolve(reader.result)
          }
          reader.readAsDataURL(xhr.response)
        }
        xhr.onerror = function () {
          resolve(undefined)
          console.error('** An error occurred during the XMLHttpRequest')
        }
        xhr.open('GET', `${url}?_=${Date.now()}`, true)
        xhr.responseType = 'blob'
        xhr.send()
      })
      // var xhr = new XMLHttpRequest();
      // xhr.onload = function () {
      //   var reader = new FileReader();
      //   reader.onloadend = function () {
      //     callback(reader.result);
      //   };
      //   reader.readAsDataURL(xhr.response);
      // };
      // xhr.open('GET', url + '?_=' + Date.now(), true);
      // xhr.responseType = 'blob';
      // xhr.send();
    },
    async createTemplate() {
      if (!this.canCreateTemplate()) {
        this.showConfirmCreateModal = false
        return
      }
      // display stopper
      // generate true QRCode
      // send store data to back end through AXIOS
      this.isPrinting = true

      // Convert images to data url to avoid cors errors

      for (const e of this.components) {
        if (e.type === 'image') {
          e.srcUrl = e.src
          e.src = await this.toDataURL(e.src)
        }
      }

      for (const e of this.components) {
        if (e.type === 'image') {
          e.src = e.srcUrl
          delete e.srcUrl
        }
      }

      const { tid } = await useTemplateStore()[TEMPLATE_EDITOR_CREATE]({ preview: '' }).catch((err) => {
        console.error(err)
        this.showConfirmCreateModal = false
      })
      logDebug(tid)
      this.message = 'successCreateTemplate'
      this.tid = tid
      this.timeout = setTimeout(() => {
        this.handleClose()
      }, 1000 * 3)
    },
    displayWarning() {
      if (!this.canCreateTemplate()) {
        this.visibleWarning = true
      }
    },
    canCreateTemplate() {
      logDebug(
        'canCreateTemplate',
        this.templateName,
        this.repeatedAttributes,
        this.hasDynamicAttributes,
      )
      return (
        this.templateName
        && !this.repeatedAttributes
        && this.hasDynamicAttributes
      )
    },
  },
}
</script>

<template>
  <div class="header">
    <div class="header-left" @click="$router.go(-1)">
      <IconArrowBack />
      <h4>{{ $t('header.title') }}</h4>
    </div>
    <input
      v-model="templateName"
      type="text"
      placeholder="Name this design"
      :class="{ warningName: checkIssueConditions }"
      style="
        min-width: 0 !important;
        width: 8.75rem !important;
        margin-right: 0.5rem;
      "
    />
    <IconEdit />
    <button
      style="position: relative; overflow: visible !important"
      :disabled="!currentLayout"
      @click="verifyCreateTemplateConditions()"
      @mouseover="displayWarning()"
      @mouseout="visibleWarning = false"
    >
      {{ $t('header.buttonCreate') }}
    </button>
    <MessageModal
      v-if="showConfirmCreateModal"
      :message="message"
      @close="handleClose()"
      @confirm="createTemplate()"
    />
  </div>
</template>

