<script>
import { useTemplateStore } from '@/store'
import { mapState, mapWritableState } from 'pinia'

export default {
  computed: {
    ...mapWritableState(useTemplateStore, ['template', 'backSide', 'activeComponent', 'selectedTab']),
    ...mapState(useTemplateStore, ['currentLayout']),
  },
  methods: {
    uploadTemplateDesign(templateSide) {
      if (templateSide === 'front') {
        this.$refs.uploadTemplateFrontDesign.click()
      } else {
        this.$refs.uploadTemplateBackDesign.click()
      }
    },
    newTemplateDesign(event, templateSide) {
      const fileReader = new FileReader()
      fileReader.onload = () => {
        useTemplateStore().setTemplateBackground({
          templateSide,
          background: fileReader.result,
        })
        event.target.value = ''
      }
      fileReader.readAsDataURL(event.target.files[0])
    },
    setTemplate(preset, templateSide) {
      useTemplateStore().setTemplate(preset)

      // this.template = {
      //   name: '',
      //   idFront: preset.id,
      //   components: JSON.parse(JSON.stringify(preset.components)),
      //   backgroundFront: '',
      //   backgroundBack: '',
      // }
      // FIXME: test this to see if necessary
      if (templateSide === 'front') {
        this.template.backgroundFront = preset.background
      } else {
        this.template.backgroundBack = preset.background
      }
    },
    isTemplate(preset) {
      return preset.id === this.template.idFront
    },
    selectTab(tab) {
      if (this.currentLayout) {
        this.selectedTab = tab
      }
    },
  },
}
</script>

<template>
  <div
    style="
      overflow: scroll !important;
      padding: 1.5rem;
      display: flex;
      flex-direction: column;
      min-height: 40rem;
    "
  >
    <div>{{ $t('backgrounds.title') }}</div>
    <input
      ref="uploadTemplateFrontDesign"
      type="file"
      style="display: none"
      accept="image/*"
      @change="newTemplateDesign($event, 'front')"
    />
    <button style="margin-top: 1.5rem" @click="uploadTemplateDesign('front')">
      {{
        currentLayout === 'Badge'
          ? $t('templates.buttonBadge')
          : $t('templates.buttonFront')
      }}
    </button>
    <input
      ref="uploadTemplateBackDesign"
      type="file"
      style="display: none"
      accept="image/*"
      @change="newTemplateDesign($event, 'back')"
    />
    <button v-if="currentLayout === 'A4'" @click="uploadTemplateDesign('back')">
      {{ $t('templates.buttonBack') }}
    </button>
  </div>
</template>

