<script>
import { useFileStore, useTemplateStore } from '@/store'

import { UPLOAD_FILE } from '@/store/actions'

export default {
  computed: {

    ...mapState(useTemplateStore, [
      'template',
      'activeComponent',
      'activeTemplateSide',
      'renderedDimensions',
      'componentIDCount',
      'uploadedSignatures',
      'components',
    ]),
  },
  methods: {
    addSignatureField() {
      const signature = {
        id: this.componentIDCount,
        type: 'signature',
        templateSide: this.activeTemplateSide,
        text: 'Signature',
        styles: {
          fontSize: 16,
          fontColor: '#000000',
          fontFamily: 'Dawning of a New Day',
        },
        isEditable: false,
        x: 50,
        y: 50,
        isDraggable: false,
      }
      this.components.push(signature)

      if (this.activeComponent.isEditable) {
        this.activeComponent.isEditable = false
      }
      useTemplateStore().setActiveComponent(signature)
    },
    uploadImage() {
      this.$refs.uploadImage.click()
    },
    async newImage(event) {
      const result = await useFileStore()[UPLOAD_FILE]({
        file: event.target.files[0],
        folder: `${this.cid}`,
      }).catch((err) => {
        console.error(err)
      })
      useTemplateStore().addUploadedSignature(result)

      // const fileReader = new FileReader()
      // fileReader.onload = () => {
      //   this.$store.commit(
      //     'addUploadedSignature',
      //     fileReader.result,
      //   )

      //   event.target.value = ''
      // }
      // fileReader.readAsDataURL(event.target.files[0])
    },
    addImage(image) {
      if (
        this.activeComponent.type === 'signature'
        && this.activeComponent.src
      ) {
        this.activeComponent.src = image
      } else {
        const imageComponent = {
          id: this.componentIDCount,
          type: 'signature',
          templateSide: this.activeTemplateSide,
          src: image,
          x: 50,
          y: 50,
          size: 0.3,
          isDraggable: false,
          isEditable: false,
          styles: {},
        }
        this.components.push(imageComponent)

        useTemplateStore().setActiveComponent(imageComponent)
      }
    },
  },
}
</script>

<template>
  <div
    style="
      display: flex;
      flex-direction: column;
      padding-top: 0;
      min-height: 40rem;
    "
  >
    <div class="signatures">
      <div>{{ $t('signatures.title') }}</div>
      <div />
      <button @click="addSignatureField">
        {{ $t('signatures.buttonAddSignature') }}
      </button>

      <hr />
      <div style="padding-top: 0">
        {{ $t('signatures.titleImage') }}
      </div>
      <input
        ref="uploadImage"
        type="file"
        style="display: none"
        accept="image/*"
        @change="newImage"
      />
      <button @click="uploadImage">
        {{ $t('signatures.buttonUpload') }}
      </button>

      <div />
      <div class="instructions">
        <strong>{{ $t('signatures.uploaded') }}</strong>
      </div>

      <div v-for="(image, index) in uploadedSignatures" :key="index">
        <img
          style="min-width: 100%; max-width: 100%"
          :src="uploadedSignatures[index]"
          alt=""
          @click="addImage(image)"
        />
      </div>
    </div>
  </div>
</template>

